:[".wrapper {\n  margin-top: 60px;\n  position: relative;\n  background: var(--white);\n  border: 1px solid #EFF1F8;\n  box-shadow: 0px 5px 80px rgba(140, 145, 181, 0.15);\n  border-radius: 20px;\n  padding: 30px;\n  padding-bottom: 70px;\n  max-height: 195px;\n  transition: .2s;\n\n  & .filtrationAddon {\n    pointer-events: none;\n    overflow: hidden;\n    height: 0;\n  }\n\n  &_opened {\n    max-height: 2000px;\n    opacity: 1;\n    transition: .2s;\n\n    & .filtrationAddon {\n      pointer-events: all;\n      height: max-content;\n      overflow: visible;\n    }\n  }\n}\n\n.inner {\n  display: flex;\n  gap: 50px;\n  justify-content: space-between;\n}\n\n.mainFiltration {\n  width: 100%;\n  display: flex;\n  gap: 20px;\n\n  & .filter {\n    width: calc(50% - 10px);\n  }\n}\n\n.actionsFiltration {\n  padding-top: 30px;\n  display: flex;\n  align-items: center;\n  gap: 20px;\n}\n\n.filter {\n  width: calc(33% - 10px);\n  align-self: flex-end;\n\n  &_main {\n    width: 50%;\n  }\n}\n\n.clear {\n  min-width: max-content;\n  color: var(--blue);\n  cursor: pointer;\n}\n\n.action {\n  width: 200px;\n}\n\n.chevron {\n  transform: rotateZ(-90deg);\n  fill: var(--blue);\n  transition: .2s;\n\n  &_rotated {\n    transform: rotateZ(0deg);\n    transition: .2s;\n\n    &Reverse {\n      transform: rotateZ(180deg);\n    transition: .2s;\n    }\n  }\n}\n\n.openedBlock {\n  position: absolute;\n  width: 100%;\n  bottom: 20px;\n  left: 0;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n}\n\n.openedBlock__control {\n  display: flex;\n  gap: 16px;\n  align-items: center;\n  cursor: pointer;\n  \n  & * {\n    color: var(--blue);\n  }\n\n  & .chevron {\n    margin-top: 5px;\n  }\n}\n\n.filtrationAddon {\n  height: max-content;\n  margin-top: 40px;\n  border-top: 1px solid var(--light-gray);\n  padding-top: 40px;\n  display: flex;\n  gap: 20px;\n  row-gap: 40px;\n  flex-wrap: wrap;\n  opacity: 0;\n  transition: .2s;\n\n  &_opened {\n    opacity: 1;\n    transition: .2s;\n  }\n}\n\n@media only screen and (max-width: 1300px) {\n  .filter {\n    width: calc(50% - 10px);\n  }\n}\n\n@media only screen and (max-width: 1000px) {\n  .filter {\n    width: calc(50% - 10px);\n  }\n\n  .inner {\n    flex-wrap: wrap;\n    row-gap: 24px;\n    justify-content: center;\n  } \n\n  .actionsFiltration {\n    padding-top: 0px;\n  }\n\n  .wrapper {\n    max-height: 270px;\n  \n    &_opened {\n      max-height: 2000px;\n    }\n  }\n}\n\n@media only screen and (max-width: 700px) {\n  .filter {\n    width: 100%;\n\n    &_main {\n      width: 100%;\n    }\n  }\n\n  .mainFiltration {\n    flex-wrap: wrap;\n\n    & .filter {\n      width: 100%;\n    }\n  }\n\n  .inner {\n    flex-wrap: wrap;\n    row-gap: 24px;\n    justify-content: center;\n  } \n\n  .actionsFiltration {\n    width: 100%;\n    flex-wrap: wrap;\n    flex-direction: column-reverse;\n    row-gap: 6px;\n  }\n\n  .wrapper {\n    max-height: 420px;\n  \n    &_opened {\n      max-height: 3300px;\n    }\n  }\n}"],"sourceRoot":""}]);
